@charset "UTF-8";
/* reset */
/* main variables */
body { margin: 0px; padding: 0px; font-family: Arial, sans-serif; background-color: #FFF; height: 100%; line-height: 1; position: relative; font-size: 13px !important; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

/*MAIN*/
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; font-size: 100%; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, main { display: block; }

blockquote, q { quotes: none; }

blockquote:before, blockquote:after, q:before, q:after { content: ''; content: none; }

table { border-collapse: collapse; border-width: 0px; padding: 0px; margin: 0px; }

html { height: 100%; -webkit-text-size-adjust: none; -ms-text-size-adjust: none; }

input, textarea { color: #333; font-family: Arial, sans-serif; outline: none; border-radius: 0; -moz-border-radius: 0; -webkit-border-radius: 0; -webkit-appearance: none; }

input[type="button"], input[type="submit"], button { cursor: pointer; }

td { margin: 0px; padding: 0px; }

form { padding: 0px; margin: 0px; }

a { color: #100da9; -webkit-transition: all 0.2s linear; -moz-transition: all 0.2s linear; -ms-transition: all 0.2s linear; -o-transition: all 0.2s linear; transition: all 0.2s linear; text-decoration: none; outline: none; -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

a, span, div, button { outline: none !important; }

input[type=submit], input[type=button], button { -webkit-appearance: none; outline: none; }

* { -webkit-box-sizing: border-box; -moz-box-sizing: border-box; box-sizing: border-box; }

*:before, *:after { -webkit-box-sizing: border-box; -moz-box-sizing: border-box; box-sizing: border-box; }

.clearfix:after, .wrapper:after, .row:after { content: "."; display: block; height: 0; clear: both; visibility: hidden; }

@font-face { font-family: 'OpenSans'; src: url("../fonts/OpenSans-Regular.ttf"); font-weight: 300; font-style: normal; font-stretch: normal; unicode-range: U+0020-FFFD; }

@font-face { font-family: 'SourceSansPro'; src: url("../fonts/SourceSansPro-Regular.ttf"); font-weight: 300; font-style: normal; font-stretch: normal; unicode-range: U+0020-FFFD; }

/* Slider */
.slick-slider { position: relative; display: block; box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }

.slick-list:focus { outline: none; }

.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; margin-left: auto; margin-right: auto; }

.slick-track:before, .slick-track:after { content: ""; display: table; }

.slick-track:after { clear: both; }

.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; display: none; }

[dir="rtl"] .slick-slide { float: right; }

.slick-slide img { display: block; }

.slick-slide.slick-loading img { display: none; }

.slick-slide.dragging img { pointer-events: none; }

.slick-initialized .slick-slide { display: block; }

.slick-loading .slick-slide { visibility: hidden; }

.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.slick-arrow.slick-hidden { display: none; }

/* Slider */
.slick-loading .slick-list { background: #fff url("./ajax-loader.gif") center center no-repeat; }

/* Icons */
@font-face { font-family: "slick"; src: url("./fonts/slick.eot"); src: url("./fonts/slick.eot?#iefix") format("embedded-opentype"), url("./fonts/slick.woff") format("woff"), url("./fonts/slick.ttf") format("truetype"), url("./fonts/slick.svg#slick") format("svg"); font-weight: normal; font-style: normal; }

/* Arrows */
.slick-prev, .slick-next { position: absolute; display: block; cursor: pointer; background: transparent; color: transparent; top: 40%; -webkit-transform: translate(0, -50%); -ms-transform: translate(0, -50%); transform: translate(0, -50%); padding: 0; z-index: 100; border: none; outline: none; }

.slick-prev:hover, .slick-prev:focus, .slick-next:hover, .slick-next:focus { outline: none; background: transparent; color: transparent; }

.slick-prev:hover:before, .slick-prev:focus:before, .slick-next:hover:before, .slick-next:focus:before { opacity: 1; }

.slick-prev.slick-disabled:before, .slick-next.slick-disabled:before { opacity: 0.25; }

.slick-prev:before, .slick-next:before { font-family: "slick"; font-size: 20px; line-height: 1; color: white; opacity: 0.75; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.slick-prev { left: 25px; }

[dir="rtl"] .slick-prev { left: auto; right: -25px; }

.slick-prev:before { content: url("../../img/prev.png"); }

[dir="rtl"] .slick-prev:before { content: url("../../img/next.png"); }

.slick-next { right: 5px; }

[dir="rtl"] .slick-next { left: -25px; right: auto; }

.slick-next:before { content: url("../../img/next.png"); }

[dir="rtl"] .slick-next:before { content: url("../../img/prev.png"); }

/* Dots */
.slick-dotted.slick-slider { margin-bottom: 30px; }

.slick-dots { position: absolute; bottom: -25px; list-style: none; display: block; text-align: center; padding: 0; margin: 0; width: 100%; }

.slick-dots li { position: relative; display: inline-block; height: 20px; width: 20px; margin: 0 5px; padding: 0; cursor: pointer; }

.slick-dots li button { border: 0; background: transparent; display: block; height: 20px; width: 20px; outline: none; line-height: 0px; font-size: 0px; color: transparent; padding: 5px; cursor: pointer; }

.slick-dots li button:hover, .slick-dots li button:focus { outline: none; }

.slick-dots li button:hover:before, .slick-dots li button:focus:before { opacity: 1; }

.slick-dots li button:before { position: absolute; top: 0; left: 0; content: "•"; width: 20px; height: 20px; font-family: "slick"; font-size: 6px; line-height: 20px; text-align: center; color: black; opacity: 0.25; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.slick-dots li.slick-active button:before { color: black; opacity: 0.75; }

/*
================================================================================
|                                     HEADER                                   |
================================================================================
*/
header { background: #f0f0f0 url(../img/bg_header.png) no-repeat; background-size: cover; width: 100%; height: 100vh; min-width: 320px; text-align: left; order: 1; -webkit-order: 1; }

.list { margin: 30px 0; list-style-type: none; display: flex; justify-content: center; align-items: center; }

.list .list-item:last-child .list-item-link { margin-right: 0; }

.list .list-item-link { font-family: 'OpenSans'; font-size: 14px; line-height: 14px; color: #fff; text-transform: uppercase; font-weight: 400; text-align: center; margin-right: 30px; }

.list .list-item-link:hover { color: #000; }

.list .list-item-link .logo { border: 1px solid #fff; padding: 5px 30px; margin-right: 30px; font-family: 'SourceSansPro'; }

.list .list-item-link .logo .logo-name { font-size: 50px; line-height: 50px; color: #fff; letter-spacing: 2px; }

.list .list-item-link .logo .logo-description { font-size: 12px; line-height: 12px; color: #fff; }

.btn { width: 185px; display: block; font-size: 14px; line-height: 20px; color: #fff; font-family: 'OpenSans'; font-weight: 400; border: 1px solid #fff; padding: 15px 30px; margin: 25px auto; box-sizing: border-box; -webkit-transition: 0.5s; -moz-transition: 0.5s; -ms-transition: 0.5s; -o-transition: 0.5s; transition: 0.5s; text-align: center; text-transform: uppercase; }

.btn:hover { background-color: #fff; color: #000; }

.slider-slick { padding: 80px 0; }

.slider-item { font-family: 'OpenSans'; text-transform: uppercase; text-align: center; display: flex; justify-content: center; align-items: center; }

.slider-item h1 { font-size: 72px; line-height: 72px; color: #fff; font-weight: 400; }

.slider-item h3 { font-size: 20px; line-height: 20px; color: #fff; font-weight: 600; }

.slider-item h3 span { color: #ff7200; }

.slider-item .slide-description { font-size: 16px; line-height: 20px; color: #fff; font-weight: 400; margin-top: 60px; }

.slider-btn-box { display: flex; justify-content: center; }

.slider-btn-box .btn { margin-left: 0; margin-right: 30px; }

.slider-btn-box .btn:last-child { margin-right: 0; }

/*
================================================================================
|                                     FOOTER                                   |
================================================================================
*/
footer { background: #000 url(../img/bg_footer.jpg) no-repeat left top; height: 80px; text-align: left; width: 100%; min-width: 320px; order: 3; -webkit-order: 3; }

.main-wrapper { padding: 0 0 0 0; min-width: 320px; width: 100%; position: relative; overflow: hidden; min-height: 100%; display: -webkit-box; display: -moz-box; display: -ms-flexbox; display: -webkit-flex; display: flex; flex-direction: column; -webkit-flex-direction: column; -ms-flex-direction: column; justify-content: flex-start; -webkit-justify-content: flex-start; -ms-flex-pack: start; }

.wrapper { min-width: 320px; max-width: 1920px; margin: 0 auto; position: relative; }

/* titles */
p { font-size: 13px; line-height: 18px; color: #000; padding: 15px 0 15px 0; font-weight: 400; }

h1 { font-size: 36px; line-height: 42px; color: #000; font-family: "Open Sans", sans-serif; padding: 15px 0 15px 0; font-weight: 400; }

h2 { font-size: 32px; line-height: 37px; color: #000; font-family: "Open Sans", sans-serif; padding: 15px 0 15px 0; font-weight: 400; }

h3 { font-size: 28px; line-height: 32px; color: #000; font-family: "Open Sans", sans-serif; padding: 15px 0 15px 0; font-weight: 400; }

/* text position */
.text-left { text-align: left !important; }

.text-center { text-align: center !important; }

.text-right { text-align: right !important; }

.nowrap { white-space: nowrap !important; }

/* loader */
.loaded .main-wrapper { visibility: hidden; }

.icon-load { background: url(../img/loader.gif) no-repeat left top; width: 40px; height: 40px; position: fixed; left: 50%; top: 50%; margin-left: -20px; margin-left: -20px; display: none; }

.loaded .icon-load { display: block; }

/*
================================================================================
|                                     CONTENT                                 |
================================================================================
*/
.content { min-width: 320px; text-align: left; width: 100%; order: 2; -webkit-order: 2; flex-grow: 1; -webkit-flex-grow: 1; background: #f9f9f9; }

.block-title { font-size: 36px; line-height: 36px; color: #666; font-family: 'OpenSans'; font-weight: 700; text-transform: uppercase; text-align: center; padding: 0; box-sizing: border-box; }

.block-title hr { width: 170px; height: 5px; background: #666; }

.block-description { width: 560px; margin: 0 auto; font-size: 14px; line-height: 20px; color: #666; font-family: 'OpenSans'; font-weight: 300; text-align: center; margin-bottom: 75px; }

.btn { border: 1px solid #ff7200; -webkit-border-radius: 3px; -moz-border-radius: 3px; border-radius: 3px; color: #ff7200; margin: 10px 30px 0 0; display: inline-block; }

.btn:last-child { background-color: #ff7200; color: #fff; }

.btn:last-child:hover { color: #ff7200; background-color: #fff; }

.btn:hover { background-color: #ff7200; color: #fff; }

.btn i { margin-right: 5px; }

/*============================= about us start ===========================*/
.about { width: 80%; margin: 100px auto; }

.about-slider { width: 46%; height: 100%; display: block; -webkit-box-shadow: 0 10px 15px rgba(0, 0, 0, 0.7); -moz-box-shadow: 0 10px 15px rgba(0, 0, 0, 0.7); -ms-box-shadow: 0 10px 15px rgba(0, 0, 0, 0.7); -o-box-shadow: 0 10px 15px rgba(0, 0, 0, 0.7); box-shadow: 0 10px 15px rgba(0, 0, 0, 0.7); }

.about-slider img { width: 100%; }

.about-content { display: flex; justify-content: space-between; }

.about-box { margin-left: 30px; }

.about-box h2 { font-size: 20px; line-height: 43px; color: #666; font-family: 'OpenSans'; font-weight: 600; padding: 0; margin-top: -10px; }

.about-box p { font-size: 14px; line-height: 18px; color: #666; font-family: 'OpenSans'; font-weight: 300; }

.about-slider .slick-arrow { width: 48px; height: 48px; top: 50%; }

.about-slider .slick-arrow:hover { background-color: rgba(0, 0, 0, 0.6); }

.about-slider .slick-prev { left: 20px; z-index: 100; }

.about-slider .slick-prev:before { content: url("../../img/about-prev.png"); }

.about-slider .slick-next { right: 20px; -webkit-transition: 0.4s; -moz-transition: 0.4s; -ms-transition: 0.4s; -o-transition: 0.4s; transition: 0.4s; }

.about-slider .slick-next:before { content: url("../../img/about-next.png"); }

/*============================= about us end ===========================*/
/*============================= amazing start ===========================*/
.amazing { width: 100%; background-image: url("../../img/bg_amazing.png"); background-size: cover; padding: 100px 10%; color: #fff; overflow: hidden; }

.amazing .block-title, .amazing .block-description { color: #fff; }

.amazing .block-title hr, .amazing .block-description hr { background-color: #fff; }

.amazing-plus-box { width: 100%; }

.amazing-plus-box-row { width: 100%; margin-bottom: 70px; display: flex; justify-content: space-between; }

.amazing-plus-box-row:last-child { margin-bottom: 0; }

.amazing-plus-box-row .amazing-plus-item { width: 32%; display: flex; justify-content: space-between; }

.amazing-plus-box-row .amazing-plus-item i { font-size: 36px; line-height: 36px; color: #ff7200; }

.amazing-plus-box-row .amazing-plus-item .amazing-plus-item-desc { width: 280px; margin-left: 25px; }

.amazing-plus-box-row .amazing-plus-item .amazing-plus-item-desc h4 { font-size: 18px; line-height: 18px; color: #fff; font-family: 'OpenSans'; font-weight: 600; }

.amazing-plus-box-row .amazing-plus-item .amazing-plus-item-desc p { font-size: 14px; line-height: 22px; color: #fff; font-family: 'OpenSans'; font-weight: 300; }

/*============================= amazing end ===========================*/
/*============================= team start ===========================*/
.team { width: 80%; margin: 100px auto; }

.team-box-row { width: 100%; display: flex; justify-content: space-between; }

.team-box-item { width: 23%; text-align: center; }

.team-box-item .team-item-img { max-width: 100%; }

.team-box-item h4 { font-size: 16px; line-height: 43px; color: #666; font-family: 'OpenSans'; font-weight: 300; }

.team-box-item h4 span { color: #ff7200; font-weight: 500; }

.team-box-item p { font-size: 16px; line-height: 22px; color: #666; font-family: 'OpenSans'; font-weight: 300; text-align: justify; }

.team-box-item .team-item-social-box { display: flex; justify-content: space-between; }

.team-box-item .team-item-social-box a { width: 44px; height: 44px; background-color: #f0f0f0; font-size: 18px; line-height: 44px; color: #666; -webkit-border-radius: 50%; -moz-border-radius: 50%; border-radius: 50%; -webkit-transition: 0.3s; -moz-transition: 0.3s; -ms-transition: 0.3s; -o-transition: 0.3s; transition: 0.3s; }

.team-box-item .team-item-social-box a:hover { background-color: #ff7200; color: #fff; }

/*============================= team end ===========================*/
/*============================= work start ===========================*/
.work { width: 100%; margin: 100px auto; }

.work-box-row { width: 100%; display: flex; justify-content: space-between; }

.work-box-item { width: 25%; height: 230px; overflow: hidden; position: relative; }

.work-box-item-desc { display: none; width: 100%; position: absolute; bottom: 0; left: 0; background-color: rgba(0, 0, 0, 0.6); }

.work-box-item-desc .button { text-align: center; padding: 50px 0; }

.work-box-item-desc .button a { display: inline-block; padding: 15px; border: 2px solid #f0f0f0; -webkit-border-radius: 50%; -moz-border-radius: 50%; border-radius: 50%; -webkit-transition: 0.3s; -moz-transition: 0.3s; -ms-transition: 0.3s; -o-transition: 0.3s; transition: 0.3s; font-size: 20px; line-height: 20px; color: #fff; margin-right: 20px; cursor: pointer; }

.work-box-item-desc .button a:last-child { margin-right: 0; }

.work-box-item-desc .button a:hover { background-color: #fff; color: #ff7200; }

.work-box-item-desc .work-item-img-desc { width: 100%; background-color: #f0f0f0; text-align: center; font-family: 'OpenSans'; font-weight: 300; }

.work-box-item-desc .work-item-img-desc h4 { font-size: 20px; line-height: 20px; color: #363636; padding-top: 20px; font-weight: normal; }

.work-box-item-desc .work-item-img-desc p { font-size: 14px; line-height: 14px; color: #666; text-transform: uppercase; }

/*============================= work end ===========================*/
/*============================= buy start ===========================*/
.buy { width: 100%; margin: 100px auto; }

.buy-content { width: 60%; margin: 0 auto; text-align: center; display: flex; flex-flow: column wrap; justify-content: center; }

.buy-desc { font-family: 'OpenSans'; font-size: 20px; line-height: 30px; color: #666; font-weight: 400; margin: 30px 0 10px 0; }

.important-text { color: #ff7e26; }

/*============================= buy end ===========================*/
/*============================= clients start ===========================*/
.clients { width: 100%; background-image: url("../../img/bg_clients.png"); background-size: cover; padding: 100px 10%; color: #f0f0f0; overflow: hidden; }

.clients .block-title, .clients .block-description { color: #f0f0f0; }

.clients .block-title hr, .clients .block-description hr { background-color: #f0f0f0; }

/*============================= clients end ===========================*/
